@use '../../../../general/main' as Gmain;
@use '../../../../themes/theme1/main' as Tmain;
@use '../../../../themes/theme1/vars';
@use 'floatingDiv';

@mixin conv_bottomBar_text(){
    
    > .converter-bottom-bar-text-main {
        position: relative;
        width: 100% ;
        height: 100%;
        display: flex;
        box-sizing: border-box;
        > * {
            box-sizing: border-box;
            padding: 0rem;
        }
        > .buttonsDiv {
            @include Tmain.TContainer_basic();
            // background-color: vars.$color-T_container;
            // box-shadow: none;
            overflow: auto;
            overflow-y:visible !important;
            &::-webkit-scrollbar {
                display: none;
            }
            box-sizing: border-box;
            display: flex;
            justify-content: space-between;
            position: relative;
            width: 100%;
            height: 100%;
            padding: 0;
            background-image: 
            linear-gradient(to right, vars.$color-T_container, vars.$color-T_container),
            linear-gradient(to right, vars.$color-T_container, vars.$color-T_container),
            linear-gradient(to right, rgba(0, 0, 0, 0.25), rgba(255, 255, 255, 0)),
            linear-gradient(to left, rgba(0, 0, 0, 0.25), rgba(255, 255, 255, 0));
            background-position: left center, right center, left center, right center;
            background-repeat: no-repeat;
            background-color: vars.$color-T_container;
            background-size: 15px 100%, 15px 100%, 10px 100%, 10px 100%;
            background-attachment: local, local, scroll, scroll;

            > button {
                @include Tmain.TButton_primary();
                box-sizing: border-box;
                padding-inline: 0rem;
                aspect-ratio: 1/1;
                height: 100%;
                background-color: transparent;
                > span {
                    color: vars.$color-primary-text;
                    font-size: 2.3rem;
                    margin: -0.05rem;  
                    &.description {
                        margin: -0.05rem;  
                        margin-bottom: -0.1rem;  
                        margin-top: 0.1rem;
                        font-size: 0.5rem;
                    }
                }
                &[state*='active']{
                    // background-color: vars.$color-primary;
                }
                @include floatingDiv.conv_floatingDiv_aligned();
                // &[id*='fontFamily']{
                    
                // }
            }
        }
        > button {
            @include Tmain.TContainer_basic();
            // background-color: vars.$color-T_container;
            // box-shadow: none;
            margin-right: 0.3em;
            user-select: none;
            position: relative;
            aspect-ratio: 1/1;
            height: 100%;
            top: 0;
            left: 0;
            padding-inline: 0;
            > span {
                font-size: 2.3rem;
            }
        }
    }
    @include floatingDiv.conv_floatingDiv_block();
}